@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Rubik";

  font-display: swap;
  src:
    url("../fonts/rubikbold.woff2") format("woff2"),
    url("../fonts/rubikbold.woff") format("woff");
}

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Rubik";

  font-display: swap;
  src:
    url("../fonts/rubikregular.woff2") format("woff2"),
    url("../fonts/rubikregular.woff") format("woff");
}
